import Vue from 'vue'
import Router from 'vue-router'
import Domain from "@/components/Domain"
import LostList from '@/components/lost-list';
import FoundList from '@/components/found-list';
import Student from '@/components/admin/student';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: Domain
    },
    {
      path:"/domain",
      name:"Domain",
      component:Domain
    },
    {
      path:"/lostlist",
      name:"LostList",
      component:LostList
    },
    {
      path:"/foundlist",
      name:"FoundList",
      component:FoundList
    },
    {
      path:"/student",
      name:"Student",
      component:Student
    }
  ]
})



