<template>
  <el-dialog
    :title="!dataForm.id ? '个人信息' : '个人信息'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    width="470px"
  >
    <el-form
      :model="dataForm"
      :rules="dataRule"
      ref="dataForm"
      label-width="50px"
      label-position="left"
      @keyup.enter.native="dataFormSubmit()"
    >
      <el-form-item label="帐号" prop="studentNo">
        <el-input placeholder="输入学号" v-model="dataForm.studentNo" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="姓名" prop="studentName">
        <el-input
          placeholder="输入姓名"
          v-model="dataForm.studentName"
          clearable
        >
        </el-input>
      </el-form-item>
      <el-form-item label="年级" prop="className">
        <el-select
          v-model="dataForm.className"
          clearable
          placeholder="请选择年级"
        >
          <el-option
            v-for="item in classOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="专业" prop="specialtyName">
        <el-select
          v-model="dataForm.specialtyName"
          clearable
          placeholder="请选择专业"
        >
          <el-option
            v-for="item in specialtyOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="QQ" prop="qq">
        <el-input placeholder="输入QQ" v-model="dataForm.qq" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="电话" prop="phone">
        <el-input placeholder="输入电话" v-model="dataForm.phone" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input placeholder="输入邮箱" v-model="dataForm.email" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input
          placeholder="输入修改密码"
          v-model="dataForm.password"
          show-password
          clearable
        >
        </el-input>
      </el-form-item>
      <el-form-item label="密码" prop="repassword">
        <el-input
          placeholder="确认密码"
          v-model="dataForm.repassword"
          show-password
          clearable
        >
        </el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
// import SingleUpload from "@/components/upload/singleUpload";
export default {
  // components: { SingleUpload },
  data() {
    return {
      visible: false,
      dataForm: {
        thingsName: "",
        thingsType: "",
        pickPlace: "",
        pickTime: "",
        thingsImg: "",
        thingsDes: "",
        storagePlace: "",
        phone: "",
        qq: "",
        email: "",
        publishTime: "",
        studentName: "",
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            },
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            },
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            },
          },
        ],
      },
      dataRule: {
        thingsName: [
          { required: true, message: "标题不能为空", trigger: "blur" },
        ],
        thingsType: [
          { required: true, message: "类型不能为空", trigger: "blur" },
        ],
        pickPlace: [
          { required: true, message: "地点不能为空", trigger: "blur" },
        ],
        pickTime: [
          { required: true, message: "时间不能为空", trigger: "blur" },
        ],
      },

      classOptions: [
        {
          value: "2016级",
          label: "2016级",
        },
        {
          value: "2017级",
          label: "2017级",
        },
        {
          value: "2018级",
          label: "2018级",
        },
        {
          value: "2019级",
          label: "2019级",
        },
        {
          value: "2020级",
          label: "2020级",
        },
        {
          value: "2021级",
          label: "2021级",
        },
      ],
      specialtyOptions: [
        {
          value: "机械设计制造及自动化",
          label: "机械设计制造及自动化",
        },
        {
          value: "网络工程",
          label: "网络工程",
        },
        {
          value: "软件工程",
          label: "软件工程",
        },
        {
          value: "土木工程",
          label: "土木工程",
        },
        {
          value: "自动化",
          label: "自动化",
        },
        {
          value: "电子信息工程",
          label: "电子信息工程",
        },
        {
          value: "电子科学与技术",
          label: "电子科学与技术",
        },
      ],
    };
  },
  methods: {
    init(id) {
      this.dataForm.brandId = id || 0;
      this.visible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].resetFields();
        if (this.dataForm.brandId) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/info/${this.dataForm.brandId}`
            ),
            method: "get",
            params: this.$http.adornParams(),
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.dataForm.name = data.brand.name;
              this.dataForm.logo = data.brand.logo;
              this.dataForm.descript = data.brand.descript;
              this.dataForm.showStatus = data.brand.showStatus;
              this.dataForm.firstLetter = data.brand.firstLetter;
              this.dataForm.sort = data.brand.sort;
            }
          });
        }
      });
    },
    // 表单提交
    dataFormSubmit() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/${!this.dataForm.brandId ? "save" : "update"}`
            ),
            method: "post",
            data: this.$http.adornData({
              brandId: this.dataForm.brandId || undefined,
              name: this.dataForm.name,
              logo: this.dataForm.logo,
              descript: this.dataForm.descript,
              showStatus: this.dataForm.showStatus,
              firstLetter: this.dataForm.firstLetter,
              sort: this.dataForm.sort,
            }),
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "操作成功",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.visible = false;
                  this.$emit("refreshDataList");
                },
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
  },
};
</script>



