<template>
  <el-dialog
    :title="!formIndex ? '登录' : '注册'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    width="370px"
    center
  >
    <el-form
      :model="dataForm"
      :rules="dataRule"
      ref="dataForm"
      v-if="formIndex == 0"
      @keyup.enter.native="dataFormSubmit()"
    >
      <el-form-item label="" prop="studentNo">
        <el-input placeholder="输入学号" v-model="dataForm.studentNo" clearable>
          <template slot="prepend">帐号</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          placeholder="输入密码"
          v-model="dataForm.password"
          show-password
          clearable
        >
          <template slot="prepend">密码</template>
        </el-input>
      </el-form-item>
    </el-form>

    <el-form
      :model="dataForm"
      :rules="dataRule"
      ref="dataForm"
      v-if="formIndex == 1"
      @keyup.enter.native="dataFormSubmit()"
    >
      <el-form-item label="" prop="studentNo">
        <el-input placeholder="输入学号" v-model="dataForm.studentNo" clearable>
          <template slot="prepend">帐号</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="studentName">
        <el-input
          placeholder="输入姓名"
          v-model="dataForm.studentName"
          clearable
        >
          <template slot="prepend">姓名</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="className">
        <el-select
          v-model="dataForm.className"
          clearable
          placeholder="请选择年级"
        >
          <el-option
            v-for="item in classOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="specialtyName">
        <el-select
          v-model="dataForm.specialtyName"
          clearable
          placeholder="请选择专业"
        >
          <el-option
            v-for="item in specialtyOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="qq">
        <el-input placeholder="输入QQ" v-model="dataForm.qq" clearable>
          <template slot="prepend">QQ</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input placeholder="输入电话" v-model="dataForm.phone" clearable>
          <template slot="prepend">电话</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          placeholder="输入密码"
          v-model="dataForm.password"
          show-password
          clearable
        >
          <template slot="prepend">密码</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="repassword">
        <el-input
          placeholder="确认密码"
          v-model="dataForm.repassword"
          show-password
          clearable
        >
          <template slot="prepend">密码</template>
        </el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button
        type="text"
        style="width: 10vw"
        @click="init(1)"
        v-if="formIndex == 0"
        >没有账号？立即注册
      </el-button>
      <el-button
        type="text"
        style="width: 10vw"
        @click="init(0)"
        v-if="formIndex == 1"
        >已有账号？立即登录
      </el-button>
      <el-button type="primary" @click="dataFormSubmit()"> 确 定 </el-button>
    </span>
  </el-dialog>
</template>

<script>
// import SingleUpload from "@/components/upload/singleUpload";
export default {
  // components: { SingleUpload },
  data() {
    var validateConfirmPassword = (rule, value, callback) => {
      if (value !== this.dataForm.password) {
        callback(new Error("两次密码不一致"));
      } else {
        callback();
      }
    };
    return {
      visible: false,
      formIndex: 0,
      dataForm: {
        studentNo: "",
        studentName: "",
        className: "",
        specialtyName: "",
        qq: "",
        phone: "",
        password: "",
        repassword: "",
      },
      dataRule: {
        studentNo: [
          { required: true, message: "学号不能为空", trigger: "blur" },
        ],
        studentName: [
          { required: true, message: "姓名不能为空", trigger: "blur" },
        ],
        className: [
          { required: true, message: "年级不能为空", trigger: "blur" },
        ],
        specialtyName: [
          { required: true, message: "专业不能为空", trigger: "blur" },
        ],
        qq: [
          {
            validator: (rule, value, callback) => {
              if (value == "") {
                callback(new Error("QQ不能为空"));
              } else if (!Number.isInteger(value) || value < 0) {
                callback(new Error("QQ必须是数字"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        phone: [
          {
            validator: (rule, value, callback) => {
              if (value == "") {
                callback(new Error("电话不能为空"));
              } else if (!Number.isInteger(value) || value < 0) {
                callback(new Error("电话必须是数字"));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
          { min: 7, max: 11, message: "长度为 7 到 11", trigger: "blur" },
        ],
        password: [
          { required: true, message: "密码不能为空", trigger: "blur" },
        ],
        repassword: [
          { required: true, message: "确认密码不能为空", trigger: "blur" },
          { validator: validateConfirmPassword, trigger: "blur" },
        ],
      },
      classOptions: [
        {
          value: "2016级",
          label: "2016级",
        },
        {
          value: "2017级",
          label: "2017级",
        },
        {
          value: "2018级",
          label: "2018级",
        },
        {
          value: "2019级",
          label: "2019级",
        },
        {
          value: "2020级",
          label: "2020级",
        },
        {
          value: "2021级",
          label: "2021级",
        },
      ],
      specialtyOptions: [
        {
          value: "机械设计制造及自动化",
          label: "机械设计制造及自动化",
        },
        {
          value: "网络工程",
          label: "网络工程",
        },
        {
          value: "软件工程",
          label: "软件工程",
        },
        {
          value: "土木工程",
          label: "土木工程",
        },
        {
          value: "自动化",
          label: "自动化",
        },
        {
          value: "电子信息工程",
          label: "电子信息工程",
        },
        {
          value: "电子科学与技术",
          label: "电子科学与技术",
        },
      ],
    };
  },
  methods: {
    init(id) {
      this.visible = true;
      this.formIndex = id || 0;
      this.$nextTick(() => {
        this.$refs["dataForm"].resetFields();
        if (this.formIndex == 1) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/info/${this.dataForm.brandId}`
            ),
            method: "get",
            params: this.$http.adornParams(),
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.dataForm.name = data.brand.name;
              this.dataForm.logo = data.brand.logo;
            }
          });
        }
      });
    },
    // 提交表单
    dataFormSubmit() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          this.$http({
            url: this.$http.adornUrl("/sys/login"),
            method: "post",
            data: this.$http.adornData({
              studentNo: this.dataForm.studentNo,
              password: this.dataForm.password,
            }),
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$cookie.set("token", data.token);
              this.$router.replace({ name: "home" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
  },
};
</script>
<style lang="">
</style>



