<template>
  <el-dialog :close-on-click-modal="false" :visible.sync="visible">
    <el-menu
      default-active="1"
      class="el-menu-demo"
      mode="horizontal"
      background-color=""
      text-color=""
      active-text-color="#ffd04b"
    >
      <el-menu-item index="1" @click="showStatus=0">招领列表</el-menu-item>
      <el-menu-item index="2" @click="showStatus=1">寻物列表</el-menu-item>
    </el-menu>
    <el-table :data="foundThings" height="300" border style="width: 100%" v-if="showStatus==1">
      <el-table-column prop="thingsType" label="类型" width="">
      </el-table-column>
      <el-table-column prop="thingsName" label="名称" width="">
      </el-table-column>
      <el-table-column prop="pickPlace" label="地点" width="">
      </el-table-column>
      <el-table-column prop="pickTime" label="时间" width=""> </el-table-column>
      <el-table-column prop="status" label="状态" width=""
        >{{ foundThings.status == 1 ? "未被认领" : "已被认领" }}
      </el-table-column>
    </el-table>
    
    <el-table :data="lostThings" height="300" border style="width: 100%" v-if="showStatus==0">
      <el-table-column prop="thingsType" label="类型" width="">
      </el-table-column>
      <el-table-column prop="thingsName" label="名称" width="">
      </el-table-column>
      <el-table-column prop="pickPlace" label="地点" width="">
      </el-table-column>
      <el-table-column prop="pickTime" label="时间" width=""> </el-table-column>
      <el-table-column prop="status" label="状态" width=""
        >{{ lostThings.status == 1 ? "未被寻到" : "已被寻到" }}
      </el-table-column>
    </el-table>
  </el-dialog>
</template>

<script>
export default {
  data() {
    return {
      visible: false,
      showStatus: 0,
      foundThings: [
        {
          thingsType: "钱包",
          thingsName: "红色的钱包",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "钱包",
          thingsName: "橙色的钱包",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "钱包",
          thingsName: "黄色的钱包",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "钱包",
          thingsName: "绿色的钱包",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "钱包",
          thingsName: "青色的钱包",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 0,
        },
      ],
      lostThings: [
        {
          thingsType: "身份证",
          thingsName: "谁的身份证",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "身份证",
          thingsName: "我的身份证",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "身份证",
          thingsName: "大家的身份证",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "身份证",
          thingsName: "身份证",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "身份证",
          thingsName: "身份证",
          pickPlace: "食堂",
          pickTime: "2021-03-09",
          status: 0,
        },
      ],
    };
  },
  methods: {
    init() {
      this.visible = true;
    },
  },
};
</script>


