<template>
  <el-dialog
    :title="!dataForm.id ? '新增' : '修改'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    width="470px"
  >
    <el-form
      :model="dataForm"
      :rules="dataRule"
      ref="dataForm"
      label-position="left"
      @keyup.enter.native="dataFormSubmit()"
      label-width="100px"
    >
      <el-form-item label="信息标题" prop="thingsName">
        <el-input
          v-model="dataForm.thingsName"
          placeholder="信息标题"
        ></el-input>
      </el-form-item>
      <el-form-item label="失物类别" prop="thingsType">
        <el-input
          v-model="dataForm.thingsType"
          placeholder="失物类别"
        ></el-input>
      </el-form-item>
      <el-form-item label="拾取时间" prop="pickTime">
        <el-date-picker
          v-model="dataForm.pickTime"
          align="right"
          type="date"
          placeholder="选择日期"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item label="拾取地点" prop="pickPlace">
        <el-input
          v-model="dataForm.pickPlace"
          placeholder="拾取地点"
        ></el-input>
      </el-form-item>
      <el-form-item label="暂存地点" prop="storagePlace">
        <el-input
          v-model="dataForm.storagePlace"
          placeholder="暂存地点"
        ></el-input>
      </el-form-item>
      <el-form-item label="物品描述" prop="descript">
        <el-input v-model="dataForm.descript" placeholder="物品描述"></el-input>
      </el-form-item>
      <el-form-item label="物品图片" prop="thingsImg">
        <!-- <el-input v-model="dataForm.thingsImg" placeholder="物品图片"></el-input> -->
        <!-- <single-upload v-model="dataForm.thingsImg"></single-upload> -->
        <el-button size="small" type="primary">点击上传</el-button>
      </el-form-item>
      <el-form-item label="联系人" prop="studentName">
        <el-input
          v-model="dataForm.studentName"
          placeholder="联系人"
        ></el-input>
      </el-form-item>
      <el-form-item label="联系电话" prop="phone">
        <el-input v-model="dataForm.phone" placeholder="联系电话"></el-input>
      </el-form-item>
      <el-form-item label="QQ" prop="qq">
        <el-input v-model="dataForm.qq" placeholder="QQ"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定</el-button>
    </span>
  </el-dialog>
</template>

<script>
// import SingleUpload from "@/components/upload/singleUpload";
export default {
  // components: { SingleUpload },
  data() {
    return {
      visible: false,
      dataForm: {
        thingsName: "",
        thingsType: "",
        pickPlace: "",
        pickTime: "",
        thingsImg: "",
        thingsDes: "",
        storagePlace: "",
        phone: "",
        qq: "",
        publishTime: "",
        studentName: "",
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            },
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            },
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            },
          },
        ],
      },
      dataRule: {
        thingsName: [
          { required: true, message: "标题不能为空", trigger: "blur" },
        ],
        thingsType: [
          { required: true, message: "类型不能为空", trigger: "blur" },
        ],
        pickPlace: [
          { required: true, message: "地点不能为空", trigger: "blur" },
        ],
        pickTime: [
          { required: true, message: "时间不能为空", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    init(id) {
      this.dataForm.brandId = id || 0;
      this.visible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].resetFields();
        if (this.dataForm.brandId) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/info/${this.dataForm.brandId}`
            ),
            method: "get",
            params: this.$http.adornParams(),
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.dataForm.name = data.brand.name;
              this.dataForm.logo = data.brand.logo;
              this.dataForm.descript = data.brand.descript;
              this.dataForm.showStatus = data.brand.showStatus;
              this.dataForm.firstLetter = data.brand.firstLetter;
              this.dataForm.sort = data.brand.sort;
            }
          });
        }
      });
    },
    // 表单提交
    dataFormSubmit() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/${!this.dataForm.brandId ? "save" : "update"}`
            ),
            method: "post",
            data: this.$http.adornData({
              brandId: this.dataForm.brandId || undefined,
              name: this.dataForm.name,
              logo: this.dataForm.logo,
              descript: this.dataForm.descript,
              showStatus: this.dataForm.showStatus,
              firstLetter: this.dataForm.firstLetter,
              sort: this.dataForm.sort,
            }),
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "操作成功",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.visible = false;
                  this.$emit("refreshDataList");
                },
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
  },
};
</script>



