<template>
  <el-dialog
    :title="!dataForm.id ? '校园失物招领平台' : '校园失物招领平台'"
    :close-on-click-modal="false"
    :visible.sync="visible"
    width="370px"
    center
  >
    <el-form
      :model="dataForm"
      ref="dataForm"
      @keyup.enter.native="dataFormSubmit()"
    >
      <el-form-item label="" prop="studentNo">
        <el-input placeholder="学号" v-model="dataForm.studentNo" :disabled="true">
          <template slot="prepend">帐号</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="studentName">
        <el-input
          placeholder="姓名"
          v-model="dataForm.studentName"
          :disabled="true"
        >
          <template slot="prepend">姓名</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="qq">
        <el-input placeholder="QQ" v-model="dataForm.qq" :disabled="true">
          <template slot="prepend">QQ</template>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input placeholder="电话" v-model="dataForm.phone" :disabled="true">
          <template slot="prepend">电话</template>
        </el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="dataFormSubmit()">确定{{ formIndex==0 ? '认领' : '寻到' }}</el-button>
    </span>
  </el-dialog>
</template>

<script>
// import SingleUpload from "@/components/upload/singleUpload";
export default {
  // components: { SingleUpload },
  data() {
    return {
      visible: false,
      formIndex: 0,
      dataForm: {
        studentNo: "",
        studentName: "",
        className: "",
        specialtyName: "",
        qq: "",
        phone: "",
        password: "",
        repassword: "",
      },
    };
  },
  methods: {
    init(id) {
      this.formIndex = id || 0;
      this.visible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].resetFields();
        if (this.formIndex) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/info/${this.dataForm.brandId}`
            ),
            method: "get",
            params: this.$http.adornParams()
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.dataForm.name = data.brand.name;
              this.dataForm.logo = data.brand.logo;
              this.dataForm.descript = data.brand.descript;
              this.dataForm.showStatus = data.brand.showStatus;
              this.dataForm.firstLetter = data.brand.firstLetter;
              this.dataForm.sort = data.brand.sort;
            }
          });
        }
      });
    },
    // 表单提交
    dataFormSubmit() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/${!this.dataForm.brandId ? "save" : "update"}`
            ),
            method: "post",
            data: this.$http.adornData({
              brandId: this.dataForm.brandId || undefined,
              name: this.dataForm.name,
              logo: this.dataForm.logo,
              descript: this.dataForm.descript,
              showStatus: this.dataForm.showStatus,
              firstLetter: this.dataForm.firstLetter,
              sort: this.dataForm.sort
            })
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "操作成功",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.visible = false;
                  this.$emit("refreshDataList");
                }
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>



