<template>
  <div id="box">
    <el-row :gutter="10" type="flex" justify="center" align="middle">
      <el-col :span="4">
        <div class="grid-content father">
          <div class="son" style="background-color: #409eff">
            <font class="title-font">校园失物招领</font>
          </div>
        </div>
      </el-col>
      <el-col :span="7">
        <div class="bg-purple-dark">
          <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
            background-color=""
            text-color=""
            active-text-color="#ffd04b"
            :router="true"
          >
            <el-menu-item index="/domain">首页</el-menu-item>
            <el-menu-item index="/foundlist">招领启事</el-menu-item>
            <el-menu-item index="/lostlist">寻物启事</el-menu-item>
            <el-menu-item index="" @click="open">发布</el-menu-item>
            <el-submenu index="">
              <template slot="title">个人信息</template>
              <el-menu-item index="" @click="personalInfoClick"
                >个人资料</el-menu-item
              >
              <el-menu-item index="" @click="personalPostInfoClick"
                >帖子信息</el-menu-item
              >
              <el-menu-item index="">退出</el-menu-item>
              <!-- <el-submenu index="2-4">
                <template slot="title">选项4</template>
                <el-menu-item index="2-4-1">选项1</el-menu-item>
                <el-menu-item index="2-4-2">选项2</el-menu-item>
              </el-submenu> -->
            </el-submenu>
          </el-menu>
        </div>
      </el-col>
      <el-col :span="2.5">
        <div class="demo-input-suffix">
          <el-input
            placeholder="搜索"
            prefix-icon="el-icon-search"
            v-model="input"
            clearable
          >
          </el-input>
        </div>
      </el-col>
      <el-col :span="2.5">
        <div class="father" v-if="showLoginVisible">
          <div class="grid-content son">
            <el-row>
              <el-button type="primary" @click="loginClick(0)">登录</el-button>
              <el-button type="primary" @click="loginClick(1)">注册</el-button>
            </el-row>
          </div>
        </div>
      </el-col>
    </el-row>

    <el-row :gutter="10" type="flex" justify="center">
      <el-col :span="14">
        <el-carousel :interval="4000" type="card" height="200px">
          <el-carousel-item v-for="(img, index) in imgs" :key="index">
            <el-image :src="img"></el-image>
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>

    <el-row :gutter="10" type="flex" justify="center">
      <el-col :span="16">
        <!-- <div class="grid-content bg-purple-dark"></div> -->
        <el-container style="height: 63vh; border: 1px solid #eee">
          <el-main>
            <router-view></router-view>
            <!--  -->
          </el-main>
        </el-container>
      </el-col>
    </el-row>

    <publish-lost v-if="publishLostVisible" ref="PublishLost"></publish-lost>
    <publish-found
      v-if="publishFoundVisible"
      ref="PublishFound"
    ></publish-found>
    <personal-info
      v-if="personalInfoVisible"
      ref="PersonalInfo"
    ></personal-info>
    <personal-post-info
      v-if="personalPostInfoVisible"
      ref="PersonalPostInfo"
    ></personal-post-info>
    <login  v-if="loginVisible" ref="Login"></login>
  </div>
</template>

<script>
// export default {
//   name: "App",
// };
import PublishLost from "./components/publishLost";
import PublishFound from "./components/publishFound";
import PersonalInfo from "./components/personal-info.vue";
import PersonalPostInfo from "./components/personal-post-info";
import Login from './components/login.vue';
export default {
  components: { PublishLost, PublishFound, PersonalInfo, PersonalPostInfo, Login },
  data() {
    return {
      activeIndex: "/domain",
      input: "",
      publishLostVisible: false,
      publishFoundVisible: false,
      personalInfoVisible: false,
      personalPostInfoVisible: false,
      showLoginVisible: true,
      loginVisible: false,
      imgs: [
        `https://gulimall-bfcod.oss-cn-shanghai.aliyuncs.com/l%26f/1.jpg`,
        `https://gulimall-bfcod.oss-cn-shanghai.aliyuncs.com/l%26f/2.jpg`,
        `https://gulimall-bfcod.oss-cn-shanghai.aliyuncs.com/l%26f/3.jpg`,
        `https://gulimall-bfcod.oss-cn-shanghai.aliyuncs.com/l%26f/4.jpg`,
        `https://gulimall-bfcod.oss-cn-shanghai.aliyuncs.com/l%26f/5.jpg`,
        `https://gulimall-bfcod.oss-cn-shanghai.aliyuncs.com/l%26f/6.jpg`,
      ],
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    open() {
      this.$confirm("选择发布类型", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "寻物启事",
        cancelButtonText: "招领启事",
        center: true,
      })
        .then(() => {
          this.publishLostVisible = true;
          this.$nextTick(() => {
            this.$refs.PublishLost.init();
          });
          this.$notify.success({
            message: this.publishLostVisible,
            showClose: false,
          });
        })
        .catch((action) => {
          if (action === "cancel") {
            this.publishFoundVisible = true;
            this.$nextTick(() => {
              this.$refs.PublishFound.init();
            });
          }
          this.$notify.success({
            message: action === "cancel" ? this.publishFoundVisible : "关闭",
            showClose: false,
          });
        });
    },
    personalInfoClick() {
      this.personalInfoVisible = true;
      this.$nextTick(() => {
        this.$refs.PersonalInfo.init();
      });
    },
    personalPostInfoClick() {
      this.personalPostInfoVisible = true;
      this.$nextTick(() => {
        this.$refs.PersonalPostInfo.init();
      });
    },
    loginClick(index){
      this.loginVisible=true;
      this.$nextTick(()=>{
        this.$refs.Login.init(index);
      });
    }
  },
};
</script>

<style>
::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 0px;
}
.el-row {
  margin-bottom: 10px;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 72px;
}
.father {
  border-radius: 4px;
  line-height: 72px;
  text-align: center;
  width: 100%;
  height: 72px;
}
.son {
  border-radius: 4px;
  display: inline-block;
  width: 100%;
  height: 72px;
}
.title-font {
  font-size: 20px;
  color: #fff;
}
.title-font2 {
  font-size: 10px;
  color: #fff;
}
</style>



