<template>
  <div v-loading="">
    <div
      style="display: flex; justify-content: space-around; flex-wrap: wrap"
      v-infinite-scroll="load"
      infinite-scroll-disabled="disabled"
    >
      <el-card
        style="width: 330px; margin-top: 10px"
        v-for="(user, index) in users"
        :key="index"
        v-loading="cardloading[index]"
        shadow="always"
      >
        <div slot="header" style="text-align: left">
          <span>{{ user.thingsName }}</span>
        </div>
        <div>
          <div>
            <el-image :src="user.thingsImg" :alt="user.thingsName" :preview-src-list="srcList" lazy></el-image>
          </div>
          <div
            style="
              text-align: left;
              color: #000;
              font-size: 12px;
              margin-top: 13px;
            "
          >
            <span>物品描述: </span><span>{{ user.thingsDes }}</span>
          </div>
          <div
            style="
              color: #000;
              font-size: 12px;
              margin-top: 13px;
            "
          >
            <el-tag
              :key="user.lostPlace"
              size="mini"
              style="margin-right: 8px"
              type="success"
              effect="plain"
            >
              {{ user.lostPlace }}
            </el-tag>
          </div>
          
          <div
            style="
              text-align: right;
              color: #000;
              font-size: 12px;
              margin-top: 13px;
            "
          >
            <el-tag size="mini" type="" effect="dark"> 寻物启事 </el-tag>
          </div>
          <div
            style="
              text-align: left;
              color: #000;
              font-size: 12px;
              margin-top: 13px;
              display: flex;
              align-items: center;
            "
          >
            
            <el-switch
              v-model="user.status"
              active-text="已认领"
              active-color="#13ce66"
              inactive-text="未认领"
              inactive-color="#ff4949"
              disabled
            >
            </el-switch>
          </div>
          <div
            style="
              text-align: left;
              color: #20a0ff;
              font-size: 12px;
              margin-top: 13px;
            "
          >
            <el-button
              type="info"
              icon="el-icon-s-custom"
              size="small"
              style="margin-left: 3px"
              @click="lookInfoClick"
              >查看发布者信息
            </el-button>
            
          </div>
          <div
            style="
              text-align: right;
              color: #20a0ff;
              font-size: 12px;
              margin-top: 13px;
            "
          >
            <!-- <span>时间: </span> -->
            <span>{{ user.publishTime | formatDate }}</span>
          </div>
        </div>
      </el-card>
    </div>
    <div v-if="loading || noMore" style="text-align: center">
      <p v-if="loading">加载中...</p>
      <p v-if="noMore">没有更多了</p>
    </div>

    <look-user-info v-if="userinfovisible" ref="lookUserInfo"></look-user-info>
  </div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import '组件名称' from '组件路径';
import { getRequest } from "../utils/api";
import { putRequest } from "../utils/api";
import { deleteRequest } from "../utils/api";
import lookUserInfo from "./look-user-info";
export default {
  components: { lookUserInfo }, // import引入或定义的局部的组件需要在此处注入到vue对象中才能使用
  props: {},
  data() {
    // 这里存放需要用到的数据
    return {
      loading: false,
      eploading: [],
      cardloading: [],
      keywords: "",
      realusers: [],
      users: [],
      allRoles: [],
      roles: [],
      cpRoles: [],
      userinfovisible: false,
      srcList: [
          'https://gulimall-bfcod.oss-cn-shanghai.aliyuncs.com/l%26f/aobama.jpg',
          'https://gulimall-bfcod.oss-cn-shanghai.aliyuncs.com/l%26f/qianbao.jpg'
        ]
    }; // 将页面需要用到的数据返回
  },
  computed: {
    noMore() {
      return this.users.length >= this.realusers.length;
    },
    disabled() {
      return this.loading || this.noMore;
    },
  }, // 存放需要实时计算数据并呈现的函数
  watch: {}, // 监控data中的数据变化
  methods: {
    saveRoles(id, index) {
      var selRoles = this.roles;
      if (this.cpRoles.length == selRoles.length) {
        for (var i = 0; i < this.cpRoles.length; i++) {
          for (var j = 0; j < selRoles.length; j++) {
            if (this.cpRoles[i].id == selRoles[j]) {
              selRoles.splice(j, 1);
              break;
            }
          }
        }
        if (selRoles.length == 0) {
          return;
        }
      }
      var _this = this;
      _this.cardloading.splice(index, 1, true);
      putRequest("lost_and_found/lostthings/list", {
        rids: this.roles,
        id: id,
      }).then(
        (resp) => {
          if (resp.status == 200 && resp.data.status == "success") {
            _this.$message({ type: resp.data.status, message: resp.data.msg });
            _this.loadOneUserById(id, index);
          } else {
            _this.cardloading.splice(index, 1, false);
            _this.$message({ type: "error", message: "更新失败!" });
          }
        },
        (resp) => {
          _this.cardloading.splice(index, 1, false);
          if (resp.response.status == 403) {
            var data = resp.response.data;
            _this.$message({ type: "error", message: data });
          }
        }
      );
    },
    showRole(aRoles, id, index) {
      this.cpRoles = aRoles;
      this.roles = [];
      this.loadRoles(index);
      for (var i = 0; i < aRoles.length; i++) {
        this.roles.push(aRoles[i].id);
      }
    },
    enabledChange(enabled, id, index) {
      var _this = this;
      _this.cardloading.splice(index, 1, true);
      putRequest("/admin/user/enabled", { enabled: enabled, uid: id }).then(
        (resp) => {
          if (resp.status != 200) {
            _this.$message({ type: "error", message: "更新失败!" });
            _this.loadOneUserById(id, index);
            return;
          }
          _this.cardloading.splice(index, 1, false);
          _this.$message({ type: "success", message: "更新成功!" });
        },
        (resp) => {
          _this.$message({ type: "error", message: "更新失败!" });
          _this.loadOneUserById(id, index);
        }
      );
    },
    loadRoles(index) {
      var _this = this;
      _this.eploading.splice(index, 1, true);
      getRequest("/lost_and_found/lostthings/list").then(
        (resp) => {
          _this.eploading.splice(index, 1, false);
          if (resp.status == 200) {
            _this.allRoles = resp.data;
          } else {
            _this.$message({ type: "error", message: "数据加载失败!" });
          }
        },
        (resp) => {
          _this.eploading.splice(index, 1, false);
          if (resp.response.status == 403) {
            var data = resp.response.data;
            _this.$message({ type: "error", message: data });
          }
        }
      );
    },
    loadOneUserById(id, index) {
      var _this = this;
      getRequest("/admin/user/" + id).then(
        (resp) => {
          _this.cardloading.splice(index, 1, false);
          if (resp.status == 200) {
            _this.users.splice(index, 1, resp.data);
          } else {
            _this.$message({ type: "error", message: "数据加载失败!" });
          }
        },
        (resp) => {
          _this.cardloading.splice(index, 1, false);
          if (resp.response.status == 403) {
            var data = resp.response.data;
            _this.$message({ type: "error", message: data });
          }
        }
      );
    },
    loadUserList() {
      var _this = this;
      getRequest("/lost_and_found/lostthings/list?key=" + this.keywords).then(
        (resp) => {
          _this.loading = false;
          if (resp.status == 200) {
            _this.realusers = resp.data.page.list;
            _this.users = _this.realusers.slice(0, 6);
            console.log(_this.realusers);
          } else {
            _this.$message({ type: "error", message: "数据加载失败!" });
          }
        },
        (resp) => {
          _this.loading = false;
          if (resp.response.status == 403) {
            var data = resp.response.data;
            _this.$message({ type: "error", message: data });
          }
        }
      );
    },
    searchClick() {
      this.loading = true;
      this.loadUserList();
    },
    load() {
      this.loading = true;
      setTimeout(() => {
        var n = 3;
        this.users = this.realusers.slice(0, this.users.length + n);
        this.loading = false;
      }, 100);
    },
    lookInfoClick() {
      this.userinfovisible = true;
      this.$nextTick(() => {
        this.$refs.lookUserInfo.init(1);
      });
    },
  }, // 方法集合
  created() {}, // 生命周期 - 创建完成（可以访问当前this实例）
  mounted() {
    this.loading = true;
    this.loadUserList();
    this.cardloading = Array.apply(null, Array(20)).map(function (item, i) {
      return false;
    });
    this.eploading = Array.apply(null, Array(20)).map(function (item, i) {
      return false;
    });
  }, // 生命周期 - 挂载完成（可以访问DOM元素）
  beforeCreate() {}, // 生命周期 - 创建之前
  beforeMount() {}, // 生命周期 - 挂载之前
  beforeUpdate() {}, // 生命周期 - 更新之前
  updated() {}, // 生命周期 - 更新之后
  beforeDestroy() {}, // 生命周期 - 销毁之前
  destroyed() {}, // 生命周期 - 销毁完成
  activated() {}, // 如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
</style>


